<template>
  <div class="edit-info">
    <h2 class="title">
      {{$route.name == '创建小说返回链接' ? '创建小说返回链接' : '修改小说返回链接'}}
    </h2>
    <Form :model="form" class="form-group" label-position="right" :label-width="100">
      <FormItem label="类型">
        <span>{{form.type == 0 ? '微信内' : '微信外'}}</span>
      </FormItem>
      <FormItem label="时间">
        <span>{{form.date}}</span>
      </FormItem>
      <FormItem label="id">
        <span>{{form.id}}</span>
      </FormItem>
      <FormItem label="文章标题">
        <Input class="inputtxt" size="large" type="text" v-model="form.title" placeholder="请输入文章标题"></Input>
      </FormItem>
      <FormItem label="阅读数">
        <span>{{form.reading}}</span>
      </FormItem>
      <FormItem label="顶部跳转">
        <Input class="inputtxt" size="large" type="text" v-model="form.linkUrl" placeholder="请输入顶部跳转链接"></Input>
      </FormItem>
      <FormItem label="banner图片">
        <Upload action="http://test.oorggt.top/tuiguang/novel/upload" :show-upload-list="false" name="imageFile" :on-success="handleSuccess1">
          <Button icon="ios-cloud-upload-outline">点击上传</Button>
          <br />
          <img v-if="form.bannerUrl" :src="form.bannerUrl" alt="" width="200" style="margin-top:10px;">
        </Upload>
      </FormItem>
      <FormItem label="内容">
        <Input class="inputtxt" size="large" type="textarea" v-model="form.capter" placeholder="请输入内容"></Input>
      </FormItem>
      <FormItem label="公号二维码" prop="picurl">
        <Upload action="http://test.oorggt.top/tuiguang/novel/upload" :show-upload-list="false" name="imageFile" :on-success="handleSuccess2">
          <Button icon="ios-cloud-upload-outline">点击上传</Button>
          <br />
          <img v-if="form.qrcode" :src="form.qrcode" alt="" width="200" style="margin-top:10px;">
        </Upload>
      </FormItem>
      <FormItem label="统计代码">
        <Input class="inputtxt" size="large" type="text" v-model="form.statisticsCode" placeholder="请输入统计代码"></Input>
      </FormItem>
      <FormItem label="其他代码">
        <Input class="inputtxt" size="large" type="text" v-model="form.otherCode" placeholder="请输入其他业务代码"></Input>
      </FormItem>
      <FormItem label="渠道">
        <Input class="inputtxt" size="large" type="text" v-model="form.channel" placeholder="请输入渠道"></Input>
      </FormItem>
      <FormItem label="备注">
        <Input class="inputtxt" size="large" type="text" v-model="form.remarks" placeholder="请输入备注"></Input>
      </FormItem>
      <FormItem>
          <Button v-if="$route.name == '创建小说返回链接'" class="edit-btn" size="large" type="primary" @click="create">立即创建</Button>
          <Button v-else class="edit-btn" size="large" type="primary" @click="save">保 存</Button>
          <Button class="edit-btn" size="large" type="warning" @click="cancel">取 消</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        date: this.formatDate(Date.now()),
        type: this.$route.query.type,
        id: this.$route.query.id,
        reading: Math.ceil(Math.random()*(150000-15000))
      },
      domain_names: [],
      bannerUrl: '',
      qrcode: ''
    }
  }, 
  mounted() {
    this.getDomain()
    if(this.$route.name == '修改小说返回链接') {
      this.getInfo()
    }
  },
  methods: {
    formatDate(time) {
      var date = new Date(Number(time));
      var year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate();
      var newTime =
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day);
      return newTime;
    },
    handleSuccess1(res, file) {
      this.form.bannerUrl =
        this.domain_names[0].domain_name + "/images/tuiguang/" + res.filename;
      // this.form.bannerUrl = "/images/tuiguang/" + res.filename;
    },
    handleSuccess2(res, file) {
      this.form.qrcode =
        this.domain_names[0].domain_name + "/images/tuiguang/" + res.filename;
      // this.form.qrcode = "/images/tuiguang/" + res.filename;
    },
    create() {
      this.$Modal.confirm({
        title: '确定创建吗？',
        onOk: () => {
          this.$axios.post('/recommend/novel/create', this.form)
          .then(res => {
            this.$Message.info(res.data.success)
            this.reset()
          })
        }
      })
    },
    save() {
      this.$Modal.confirm({
        title: '确认修改吗？',
        onOk: () => {
          this.$axios.post('/recommend/novel/update', this.form)
          .then(res => {
            this.$Message.info(res.data.success)
            this.reset()
          })
        }
      })
    },
    cancel() {
      this.$Modal.confirm({
        title: '确认取消操作吗？',
        onOk: () => {
          this.reset()
        }
      })
    },
    reset() {
      console.log(this.$route.query._id)
      this.$router.push({
        name: '修改小说返回列表',
        query: {id: this.form.id, type:this.form.type, _id: this.$route.query._id}
      })
      this.form = {
        date: this.formatDate(Date.now()),
        type: this.$route.query.type,
        id: this.$route.query.id,
        reading: Math.ceil(Math.random()*(150000-15000))
      }
    },
    getDomain() {
      this.$axios.get("/tuiguang/novel/show").then(res => {
        this.domain_names = res.data.domain_names;
      });
    },
    getInfo() {
      this.$axios.get('/recommend/novel', {params: {id: this.$route.query.id}})
      .then(res => {
        if(res.data.success) {
          this.form = res.data.data
        }
      })
    }
  }
}
</script>

<style scoped>
.form-group {
  text-align: left;
  width: 500px;
  margin: 0 auto;
}
</style>
